import { BaseClass } from './BaseClass'

export interface InlineQueryResulstButton {
    text: string
    web_app: WebAppInfo
    start_parameter: string
}

export interface WebAppInfo {
    url: string
}

export interface InlineQueryAnswer { 
    results: Array<InlineQueryAnswer>
    button?: InlineQueryResulstButton
    cache_time?: number
    is_personal?: boolean
    next_offset?: string
}

export class InlineQueryAnswer {

    public constructor(packet: InlineQueryAnswer) {
        BaseClass.defaults(packet, this)
    }

    public parse(): object {
        return { ...InlineQueryAnswer }
    }

}